* {
    margin: 0;
    padding: 0;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
  
:root {
    --primary-color: #33435e;
    --secondary-color: #333348;
    --tertiary-color:  #f0f8ff;
    --quaternary-color:  #fffbca;
}
  
/* initial styling for header */
header {
    color: var(--quaternary-color);
    background-color: var(--secondary-color);
    width: 100%;
}

header a {
    text-decoration: none;
    color: var(--quaternary-color);
    font-style: italic;
}

nav a {
    margin: 5px;
    padding: 5px;
}


/* end header styling */
.card {
    width: 100%;
    height: 15rem;
    padding: 5px;
    margin: 10px;
    background-color: var(--tertiary-color);
    text-align: center;
}

footer {
    background-color: var(--primary-color);
    color: var(--quaternary-color);
    text-align: center;
}

.fyg {
    background-image: url('./components/assets/images/fyg.jpg');
    background-repeat: no-repeat;
    background-attachment:inherit;
    background-position-y: top;
    background-position-x: center;
    background-size: 340px;
    color: var(--tertiary-color);
}

.niche {
    background-image: url('./components/assets/images/niche.jpg');
    background-repeat: no-repeat;
    background-attachment:inherit;
    background-position-y: top;
    background-position-x: center;
    background-size: 340px;
    color: var(--tertiary-color);
}